import axios from "axios";
import QS from "qs"

// 设置默认前缀
axios.defaults.baseURL = "https://www.fastmock.site/mock/5678cc755476850b24d97b29382e6255/quickbuymall";

// 封装get请求
export const get = (url,params)=>{
    return new Promise((resolve, reject) => {
        axios.get(url,{params}).then(res=>{
            resolve(res.data);
        }).catch(err=>{
            reject(err.data);
        })
    });
}

// 封装post请求
export const post = (url,params)=>{
    return new Promise((resolve, reject) => {
        axios.post(url,QS.stringify(params)).then(res=>{
            resolve(res.data);
        }).catch(err=>{
            reject(err.data);
        })
    });
}
